import React, { Component } from 'react';
import { Grid, Row, Col, Navbar, Panel } from 'react-bootstrap';
import './App.css';
const icons = require.context('./icons/');

function Icon({ hero }) {
  let src = icons(`./80px-Icon_Portrait_${hero.name}.png`);
  return <img src={src} alt={hero.name} />;
}

function MatchSummary({ match, selected, onClick }) {
  function percentage(prob) {
    return Math.round(prob*100);
  };
  return (
    <Panel
      className="MatchSummary"
      header={`Match ${match.num}`}
      bsStyle={selected ? "primary" : "default"}
      onClick={onClick}>
      <Row>
        <Col xs={5}>
          <div className="MatchSummary-pic">
            <Icon hero={match.left} />
          </div>
        </Col>
        <Col xs={5} xsOffset={2}>
          <div className="MatchSummary-pic">
            <Icon hero={match.right} />
          </div>
        </Col>
      </Row>
      <Row>
        <Col xs={5}>
          <div className="MatchSummary-name">{match.left.name}</div>
        </Col>
        <Col xs={2}>
          <div className="MatchSummary-vs">VS</div>
        </Col>
        <Col xs={5}>
          <div className="MatchSummary-name">{match.right.name}</div>
        </Col>
      </Row>
      <Row>
        <Col xs={5}>
          <div className="MatchSummary-winprob">
            {percentage(match.right.winprob)}%
          </div>
        </Col>
        <Col xs={5} xsOffset={2}>
          <div className="MatchSummary-winprob">
            {percentage(match.right.winprob)}%
          </div>
        </Col>
      </Row>
    </Panel>
  );
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: (props.length === 1 ? 0 : null),
    };
  }

  selectMatch(i) {
    this.setState({ selected: i });
  }

  render() {
    // TODO make it look reasonable in later rounds when there are fewer matches
    let match_summaries = this.props.data.match_summaries.map((match, i) => (
      <Col sm={6} lg={3}>
        <MatchSummary
          match={match}
          selected={this.state.selected === i}
          onClick={this.selectMatch.bind(this, i)}
        />
      </Col>
    ));
    return (
      <div className="App">
        <Navbar inverse>
          <Grid>
            <Navbar.Header>
              <Navbar.Brand>
                <a href="/">FEH Voting Gauntlet Score Predictions</a>
              </Navbar.Brand>
              <Navbar.Toggle />
            </Navbar.Header>
          </Grid>
        </Navbar>
        <Grid>
          <Row>{match_summaries}</Row>
        </Grid>
      </div>
    );
  }
}

export default App;



